.container {
  height: 80px;
  display: flex;
}
.rotate {
  overflow: hidden;
}

.word {
  display: block;
  height: 100%;
  padding-left: 10px;
  color: #ae00ff;
  animation: rotate_words 6s infinite;
}

@keyframes rotate_words {
  10% {
    transform: translateY(-75%);
  }
  25% {
    padding-top: 2rem;

    transform: translateY(-79%);
  }
  35% {
    padding-top: 2rem;
    transform: translateY(-176%);
  }
  50% {
    padding-top: 2rem;
    transform: translateY(-180%);
  }
  60% {
    padding-top: 2rem;
    transform: translateY(-275%);
  }
  75% {
    padding-top: 2rem;
    transform: translateY(-280%);
  }
  85% {
    padding-top: 2rem;
    transform: translateY(-373%);
  }
  100% {
    padding-top: 2rem;
    transform: translateY(-380%);
  }
}

.animated-text{
  color: #fff;
  position: absolute;
  transform: translate(-50%,-50%);
  background: transparent;
  padding: 0 40px;
  height: 60px;
  overflow: hidden;
}

.line{
  /* text-transform: uppercase; */
  text-align: center;
  /* font-size: 40px; */
  line-height: 60px;
}

/* .line:first-child{
  animation: a 8s infinite 2s;
} */

.line:nth-child(1) {
  animation: a 8s infinite 0s;
}

.line:nth-child(2) {
  animation: a 8s infinite 2s;
}

.line:nth-child(3) {
  animation: a 8s infinite 4s;
}


@keyframes a{
  0%{
      margin-top: 0px;
  }
  16%{
      margin-top: -60px;
  }
  33%{
      margin-top: -120px;
  }
  50%{
      margin-top: -180px;
  }
  66%{
      margin-top: -120px;
  }
  82%{
      margin-top: -60px;
  }
  100%{
      margin-top: 0px;
  }
}